import axios from 'axios'

// eslint-disable-next-line camelcase
export const getCate_api = (obj) => {
  return axios.get('/categories', { params: obj })
}

// eslint-disable-next-line camelcase
export const getCatePost_api = (obj) => {
  return axios.post('/categories', obj)
}

// eslint-disable-next-line camelcase
export const getAddShop_api = async (obj) => {
  return axios.get(`categories/${obj.id}/attributes`, { params: { sel: obj.sel } })
}
