<template>
  <div class="goods">
    <breaks first="商品管理" scenter="添加商品"></breaks>
    <el-card class="box-card">
      <el-alert
        title="消息提示的文案"
        type="info"
        :closable="false"
        center
        show-icon>
      </el-alert>
      <el-steps :active="activeName-0" align-center finish-status="success">
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <el-tabs v-model="activeName" tabPosition="left" :before-leave="beforeLeave">
        <el-tab-pane label="基本信息" name="0">
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="ruleForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model.number="ruleForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model.number="ruleForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model.number="ruleForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader
                v-model="ruleForm.goods_cat"
                :options="options"
                :props="props"
                @change="handleChange"
              ></el-cascader>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="商品参数" name="1" :data="getform">
          <ul>
            <li v-for="item in getform" :key="item.attr_id">
              <div>{{ item.attr_name }}的快递</div>
              <el-button type="primary" v-show="item2.length" v-for="(item2, index) in item.attr_vals" :key="index">
                {{ item2 }}
              </el-button>
            </li>
          </ul>
        </el-tab-pane>
        <el-tab-pane label="商品属性" name="2">
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="demo-ruleForm">
            <el-form-item v-for="itme in attributes" :key="itme.attr_id" :label="itme.attr_name">
              <el-input v-model="itme.attr_vals"></el-input>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="商品图片" name="3">
          <el-upload
            class="upload-demo"
            action="http://192.168.68.103:8888/api/private/v1/upload"
            :headers="headers"
            :multiple="true"
            :on-success="onSuccess"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :file-list="fileList"
            list-type="picture">
            <el-button size="small" type="primary">点击上传</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
          </el-upload>
        </el-tab-pane>
        <el-tab-pane label="商品内容" name="4">
          <quill-editor v-model="ruleForm.goods_introduce"></quill-editor>
          <el-button type="primary" @click="addShoping">添加商品</el-button>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>
<script>
// eslint-disable-next-line camelcase
import { getAddShop_api } from '@/api'

import { quillEditor } from 'vue-quill-editor'

import { cloneDeep } from 'lodash'

export default {
  data () {
    return {
      tabPosition: 'left',
      activeName: '0',
      ruleForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat: [],
        attrs: [],
        pics: [],
        goods_introduce: ''
      },
      editorOption: {},
      form: {},
      getform: [],
      options: [],
      props: {
        label: 'cat_name',
        children: 'children',
        value: 'cat_id'
      },
      addForm: {},
      fileList: [],
      headers: { Authorization: localStorage.getItem('token') },
      attributes: [],
      rules: {
        goods_name: [
          {
            required: true,
            message: '请输入登录名称',
            trigger: 'blur'
          }
        ],
        goods_price: [
          {
            required: true,
            message: '请输入登录名称',
            trigger: 'blur'
          }
        ],
        goods_weight: [
          {
            required: true,
            message: '请输入登录名称',
            trigger: 'blur'
          }
        ],
        goods_number: [
          {
            required: true,
            message: '请输入登录名称',
            trigger: 'blur'
          }
        ],
        goods_cat: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created () {
    this.getAdd()
  },
  methods: {
    // 联级选择器里的参数
    async getAdd () {
      const res = await this.$http('/categories', { params: { type: 3 } })
      this.options = res.data.data
    },
    async beforeLeave (activeName, oldActiveName) {
      if (this.valueChang) {
        this.$message.error('请先选择商品分类！')
        return new Promise()
      }
      if (activeName === '1') {
        const res1 = await getAddShop_api({
          id: this.cardId,
          sel: 'many'
        })
        res1.data.data.forEach(itme => {
          itme.attr_vals = itme.attr_vals.split(',')
        })
        this.getform = res1.data.data
        console.log(this.getform)
      } else if (activeName === '2') {
        const res2 = await getAddShop_api({
          id: this.cardId,
          sel: 'only'
        })
        this.attributes = res2.data.data
      }
    },
    handleChange () {
      if (this.valueChang) {
        this.ruleForm.goods_cat = []
      }
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
    },
    // 图片上传成功后的回调
    onSuccess (response, file, fileList) {
      this.ruleForm.pics.push({ pic: response.data.tmp_path })
    },
    // 添加商品，发送post请求
    async addShoping () {
      console.log(this.ruleForm)
      const newArr = this.getform.map(item => {
        return {
          attr_id: item.attr_id,
          attr_value: item.attr_vals.join(',')
        }
      })
      const newArr2 = this.attributes.map(item => {
        return {
          attr_id: item.attr_id,
          attr_value: item.attr_vals
        }
      })
      this.ruleForm.attrs.push(...newArr, ...newArr2)
      console.log(this.ruleForm)
      const newFrom = cloneDeep(this.ruleForm)
      newFrom.goods_cat = newFrom.goods_cat.join(',')
      console.log(newFrom)
      const res = await this.$http.post('/goods', newFrom)
      console.log(res)
      if (res.data.meta.status === 201) {
        this.$message({
          message: res.data.meta.msg,
          type: 'success'
        })
        this.$router.push('/goods')
      } else {
        this.$message.error(res.data.meta.msg)
      }
    }
  },
  computed: {
    valueChang () {
      if (this.ruleForm.goods_cat.length !== 3) {
        return true
      } else {
        return false
      }
    },
    cardId () {
      return this.ruleForm.goods_cat[this.ruleForm.goods_cat.length - 1]
    }
  },
  components: {
    quillEditor
  }
}
</script>
<style lang="less" scoped>
.goods {
  width: 100%;
  height: 100%;

  .el-card {
    .el-steps {
      margin: 20px 0;
    }

    /deep/ .el-form-item__label {
      padding-bottom: 10px;
      line-height: 40px;
      width: 100%;
      text-align: left;
    }

    /deep/ .ql-editor {
      height: 400px;
    }

    .el-button {
      margin-top: 15px;
    }
  }
}
</style>
