<template>
  <div class="categories">
    <breaks first="商品管理" scenter="商品分类"></breaks>
    <el-card class="box-card">
      <el-button type="primary" @click="getAdd">添加分类</el-button>
      <!-- 表格 -->
      <zk-table
        ref="table"
        :border="true"
        :columns="columns"
        :data="data"
        :expand-type="false"
        :selection-type="false"
        :tree-type="true"
        index-text="#"
        show-index>

        <template slot="sex" scope="scoped">
          <i v-if="!scoped.row.cat_deleted" class="el-icon-error" style="color: red;"></i>
          <i v-else class="el-icon-success" style="color: #39ce39;"></i>
        </template>

        <template slot="score" scope="scoped">
          <el-button v-if="scoped.row.cat_level==0" size="mini" type="primary">一级</el-button>
          <el-button v-else-if="scoped.row.cat_level==1" size="mini" type="success">二级</el-button>
          <el-button v-else-if="scoped.row.cat_level==2" size="mini" type="warning">三级</el-button>
        </template>

        <template slot="likes" scope="scoped">
          <el-button :aa="scoped.row" icon="el-icon-edit" size="mini" type="primary">
            编辑
          </el-button>
          <el-button icon="el-icon-delete" size="mini" type="danger">删除
          </el-button>
        </template>
      </zk-table>
      <!--      分页-->
      <el-pagination
        :current-page="params.pagenum"
        :page-size="params.pagesize"
        :page-sizes="[1, 2, 5, 10]"
        :total="totals"
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
      </el-pagination>
      <el-dialog :visible.sync="dialogFormVisible" title="添加商品分类">
        <el-form ref="ruleForm" :model="form" :rules="rules">
          <el-form-item :label-width="formLabelWidth" label="分类名称" prop="cat_name">
            <el-input v-model="form.cat_name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item :label-width="formLabelWidth" label="父级分类" prop="cat_level">
            <el-cascader
              v-model="value"
              :options="options"
              :props="props"
              style="width:100%"
            ></el-cascader>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="entAdd">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
// eslint-disable-next-line camelcase
import { getCate_api, getCatePost_api } from '@/api/index'

export default {
  data () {
    return {
      data: [],
      totals: 0,
      params: {
        pagenum: 1,
        pagesize: 5,
        type: 3
      },
      value: [],
      props: {
        label: 'cat_name',
        children: 'children',
        value: 'cat_id'
      },
      options: [],
      form: {
        cat_name: '',
        cat_pid: 0,
        cat_level: 0
      },
      formLabelWidth: '120px',
      dialogFormVisible: false,
      dialohUpdate: false,
      columns: [
        {
          label: '分类名称',
          prop: 'cat_name',
          width: '400px'
        },
        {
          label: '是否有效',
          prop: 'cat_deleted',
          minWidth: '50px',
          type: 'template',
          template: 'sex'
        },
        {
          label: '排序',
          prop: 'cat_level',
          type: 'template',
          template: 'score'
        },
        {
          label: '操作',
          prop: 'likes',
          minWidth: '200px',
          type: 'template',
          template: 'likes'
        }
      ],
      rules: {
        cat_name: [
          {
            required: true,
            message: '请输入分类名称',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created () {
    this.shoplist()
  },
  methods: {
    async shoplist () {
      const datas = await getCate_api(this.params)
      const res = datas.data
      this.data = res.data.result
      this.totals = res.data.total
    },
    handleSizeChange (val) {
      this.params.pagesize = val
      this.shoplist()
    },
    handleCurrentChange (val) {
      this.params.pagenum = val
      this.shoplist()
    },
    async getAdd () {
      const res = await getCate_api({ type: 2 })
      console.log(res)
      this.options = res.data.data
      this.dialogFormVisible = true
    },
    entAdd () {
      this.$refs.ruleForm.validate(async (valid) => {
        if (valid) {
          // 获取三个参数
          this.form.cat_pid = this.value.length === 0 ? 0 : this.value[this.value.length - 1]
          this.form.cat_level = this.value.length
          const res = await getCatePost_api(this.form)
          const resd = res.data
          if (resd.meta.status === 201) {
            this.$message({
              message: resd.meta.msg,
              type: 'success'
            })
            this.dialogFormVisible = false
            this.$refs.ruleForm.resetFields()
            this.shoplist()
          } else {
            this.$message.error(resd.meta.msg)
          }
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.zk-table {
  margin: 15px 0;
}
</style>
