<template>
  <div>
    <breaks first="商品管理" scenter="参数列表"></breaks>
    <el-card class="box-card">
      <el-alert
        title="注意：只允许为第三级分类设置相关参数！"
        type="warning"
        :closable="false"
        show-icon>
      </el-alert>
      <el-form label-width="100px" class="demo-ruleForm">
        <el-form-item label="选择商品分类:">
          <el-cascader
            v-model="value"
            :options="options"
            :props="props"
            @change="handleChange"></el-cascader>
        </el-form-item>
      </el-form>
      <el-tabs v-model="activeName" @tab-click="tabClick">
        <el-tab-pane label="动态属性" name="only">
          <el-button type="primary" size="mini" :disabled="disabled">添加参数</el-button>
          <el-table
            :data="tableData"
            style="width: 100%"
            :border="true">
            <el-table-column
              type="expand"
              width="50">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <el-tag
                    :key="tag"
                    v-for="tag in props.row.attr_vals"
                    closable
                    :disable-transitions="false"
                    @close="handleClose(tag, props.row)">
                    {{ tag }}
                  </el-tag>
                  <el-input
                    class="input-new-tag"
                    v-if="props.row.inputVisible"
                    v-model="props.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm"
                    @blur="handleInputConfirm(props.row)"
                  >
                  </el-input>
                  <el-button v-else class="button-new-tag" size="small" @click="showInput(props.row)">+ New Tag
                  </el-button>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column
              label="#"
              width="50" type="index">
            </el-table-column>
            <el-table-column label="属性名称" width="670">
              <template slot-scope="scope">
                {{ scope.row.attr_name }}
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  icon="el-icon-edit" size="mini" type="primary"
                  @click="handleEdit(scope.$index, scope.row)">编辑
                </el-button>
                <el-button
                  icon="el-icon-delete" size="mini" type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name="many">
          <el-button type="primary" size="mini" :disabled="disabled">添加参数</el-button>
          <el-table
            :data="tableData"
            style="width: 100%"
            :border="true">
            <el-table-column
              type="expand"
              width="50">
              <template slot-scope="props">
                <el-form label-position="left" inline class="demo-table-expand">
                  <el-tag
                    :key="tag"
                    v-for="tag in props.row.attr_vals"
                    closable
                    :disable-transitions="false"
                    @close="handleClose(tag, props.row)">
                    {{ tag }}
                  </el-tag>
                  <el-input
                    class="input-new-tag"
                    v-if="props.row.inputVisible"
                    v-model="props.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm"
                    @blur="handleInputConfirm(props.row)"
                  >
                  </el-input>
                  <el-button v-else class="button-new-tag" size="small" @click="showInput(props.row)">+ New Tag
                  </el-button>
                </el-form>
              </template>
            </el-table-column>
            <el-table-column
              label="#"
              width="50" type="index">
            </el-table-column>
            <el-table-column label="属性名称" width="670">
              <template slot-scope="scope">
                {{ scope.row.attr_name }}
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  @click="handleEdit(scope.$index, scope.row)">编辑
                </el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      value: [],
      options: [],
      props: {
        label: 'cat_name',
        children: 'children',
        value: 'cat_id',
        expandTrigger: 'hover'
      },
      activeName: 'only',
      tableData: [],
      disabled: true,
      obj: {}
    }
  },
  created () {
    this.getCat()
  },
  methods: {
    handleChange (value) {
      if (value.length !== 3) {
        this.value.length = 0
        this.disabled = true
        this.tableData = []
      } else {
        this.getParameter()
      }
    },
    async getParameter () {
      if (this.value.length !== 0) {
        this.disabled = false
        const res = await this.$http(`categories/${this.getCatId}/attributes`, { params: { sel: this.activeName } })
        if (res.data.meta.status === 200) {
          this.$message.success(res.data.meta.msg)
          console.log(res.data.data)
          res.data.data.forEach(item => {
            item.attr_vals = item.attr_vals.split(' ').filter(item2 => {
              return item2 !== ''
            })
            item.inputVisible = false
            item.inputValue = ''
          })
          this.tableData = res.data.data
        } else {
          this.$message.error(res.data.meta.msg)
        }
      }
    },
    // 获取参数分类信息
    async getCat () {
      const res = await this.$http('/categories', { params: { type: 3 } })
      this.options = res.data.data
    },
    handleEdit (index, row) {
      console.log(index, row)
    },
    handleDelete (index, row) {
      console.log(index, row)
    },
    tabClick () {
      this.getParameter()
    },
    handleClose (tag, rows) {
      rows.attr_vals.splice(rows.attr_vals.indexOf(tag), 1)
      this.getRows(rows)
    },
    showInput (rows) {
      rows.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },

    handleInputConfirm (rows) {
      const inputValue = rows.inputValue
      if (inputValue) {
        rows.attr_vals.push(inputValue)
        this.getRows(rows)
      }
      rows.inputVisible = false
      rows.inputValue = ''
    },
    async getRows (rows) {
      this.obj = {
        attr_name: rows.attr_name,
        attr_sel: this.activeName,
        attr_vals: rows.attr_vals.join(' ')
      }
      const res = await this.$http.put(`categories/${this.getCatId}/attributes/${rows.attr_id}`, this.obj)
      if (res.data.meta.status === 200) {
        this.$message.success(res.data.meta.msg)
      } else {
        this.$message.error(res.data.meta.msg)
      }
    }
  },
  computed: {
    getCatId () {
      return this.value[this.value.length - 1]
    }
  }
}
</script>

<style lang="less" scoped>
.el-card {
  .el-form-item {
    margin: 22px 0;
  }

  .el-table {
    margin-top: 15px;

    .el-tag + .el-tag {
      margin-left: 10px;
    }

    .button-new-tag {
      margin-left: 10px;
      height: 32px;
      line-height: 30px;
      padding-top: 0;
      padding-bottom: 0;
    }

    .input-new-tag {
      width: 90px;
      margin-left: 10px;
      vertical-align: bottom;
    }
  }
}
</style>
